<template>
  <div style="position: relative; width: 100%; height: 100%">
    <!-- Fabric.js  -->

    <div>
      <h2> </h2>
      <VisitorCounter></VisitorCounter>
    </div>
    <canvas
      ref="canvas"
      style="border: 1px solid #ccc; width: 100%; height: 100%"
    ></canvas>

    <!--   -->
    <div
      class="edit-panel"
      :class="{ open: panelOpen }"
      @touchstart="onTouchStart"
      @touchmove="onTouchMove"
      @touchend="onTouchEnd"
    >
      <div class="panel-handle" @click="togglePanel"></div>
      <div class="panel-content">
        <!--    -->
        <button
          v-for="img in StickerList"
          :key="img.src"
          @click="addImage(img.src)"
          style="background: none; border: none; cursor: pointer"
        >
          <img :src="img.src" :alt="img.label" width="32" height="32" />
        </button>
        <!--    -->
        <button @click="addRectangle"> </button>
        <button @click="addTextbox"> </button>
        <!--     -->
        <button
          v-for="(img, idx) in backroundList"
          :key="'replace-img-' + idx"
          @click="replaceDefaultImage(img.src)"
        >
             {{ idx + 1 }}
        </button>
        <!--    -->
        <button @click="triggerFileInput"> </button>
        <input
          ref="fileInput"
          type="file"
          accept="image/*"
          style="display: none"
          @change="onFileChange"
        />
        <!-- SVG    () -->
        <div>
          <button
            v-for="(btn, idx) in colorButtonList"
            :key="btn.label + idx"
            @click="changeSvgColorsByIds(btn.colors)"
          >
            {{ btn.label }}
          </button>
        </div>
        <!--     -->
        <div style="display: flex; gap: 8px">
          <button
            v-for="clothes in clothesList"
            :key="clothes.name"
            @click="selectClothes(clothes)"
            style="background: none; border: none; cursor: pointer"
          >
            <img
              :src="clothes.icon"
              :alt="clothes.name"
              width="48"
              height="48"
            />
            <div>{{ clothes.name }}</div>
          </button>
        </div>
        <!--     (   ) -->
        <div v-if="selectedClothes" style="margin: 12px 0">
          <button
            v-for="(btn, idx) in selectedClothes.colorButtons"
            :key="btn.label + idx"
            @click="changeClothesColors(btn.targets, selectedClothes.name, idx)"
            style="display: flex; align-items: center; gap: 8px"
          >
            <span
              v-if="btn.mainColor"
              :style="{
                display: 'inline-block',
                width: '18px',
                height: '18px',
                borderRadius: '50%',
                background: btn.mainColor,
                border: '1px solid #aaa',
              }"
            ></span>
            {{ btn.label }}
          </button>
        </div>
        <!--    -->
        <div style="display: flex; gap: 8px; margin-bottom: 12px">
          <button
            v-for="pose in ['', '']"
            :key="pose"
            :style="{
              background: currentPose === pose ? '#00bfff' : '#eee',
              color: currentPose === pose ? '#fff' : '#333',
              border: '1px solid #ccc',
              borderRadius: '6px',
              padding: '6px 16px',
              cursor: 'pointer',
            }"
            @click="changePose(pose)"
          >
            {{ pose }}
          </button>
        </div>
        <!--    -->
        <button @click="saveCanvasAsImage"> </button>
      </div>
    </div>
  </div>
</template>
<script>
// , SVG   import
const req = require.context("@/assets/image", false, /\.png$/);
const additionalImages = req.keys().map((key, idx) => ({
  src: req(key),
  label: `emoji${idx + 1}`,
}));

import { ref, onMounted, markRaw } from "vue";
import {
  Canvas,
  Rect,
  Image as FabricImage,
  Control,
  util,
  controlsUtils,
  loadSVGFromURL,
  Group,
  IText,
} from "fabric";

import image4 from "@/assets/image/backround/ (1).jpg";
import image5 from "@/assets/image/backround/ (2).jpg";
import image6 from "@/assets/image/backround/ (4).jpg";
import deleteIcon from "@/assets/image/ui/deleteIcon.png";
import rotateIcon from "@/assets/image/ui/cached.svg";
import resizeIcon from "@/assets/image/ui/open_in_full.svg";
import human from "@/assets/image/.svg";
import humanArm from "@/assets/image/.svg";
import VisitorCounter from "@/components/VisitorCounter.vue";

// Vue CLI/webpack require.context 
const svgContext = require.context("@/assets/image", true, /\.(svg|png|jpg)$/);

function getImageUrl(filename) {
  try {
    return svgContext("./" + filename);
  } catch {
    return "";
  }
}

import backroundDataRaw from "@/assets/data/backround.json";
const backroundList = (backroundDataRaw.default || backroundDataRaw).map(
  (item) => ({
    ...item,
    src: getImageUrl(item.src),
  })
);
import stickerDataRaw from "@/assets/data/sticker.json";
const StickerList = (stickerDataRaw.default || stickerDataRaw).map((item) => ({
  ...item,
  src: getImageUrl(item.src),
}));

import clothesDataRaw from "@/assets/data/clothes.json";
const clothesData = clothesDataRaw.default || clothesDataRaw;
const clothesListRaw = clothesData.clothesList || [];
const clothesList = clothesListRaw.map((item) => ({
  ...item,
  icon: getImageUrl(item.icon),
  svgUrl: Object.fromEntries(
    Object.entries(item.svgUrl).map(([k, v]) => [k, getImageUrl(v)])
  ),
}));

export default {
  data() {
    return {
      // Fabric.js  
      canvas: null,
      //   
      isRestoring: false,
      //   
      backroundList,
      //     
      defaultImageObject: null,
      //   
      currentPose: "",
      // 
      StickerList,
      //   ( SVG, ,   )
      clothesList,
      //    
      selectedClothes: null,
      // { [clothes.name]: index }
      selectedColorIndexes: {},
      //    
      panelOpen: false,
      touchStartY: 0,
      touchMoveY: 0,
    };
  },
  components: {
    VisitorCounter,
  },
  methods: {
    togglePanel() {
      this.panelOpen = !this.panelOpen;
    },
    onTouchStart(e) {
      this.touchStartY = e.touches[0].clientY;
    },
    onTouchMove(e) {
      this.touchMoveY = e.touches[0].clientY;
    },
    onTouchEnd() {
      const delta = this.touchStartY - this.touchMoveY;
      if (delta > 30) {
        //  : 
        this.panelOpen = true;
      } else if (delta < -30) {
        //  : 
        this.panelOpen = false;
      }
    },
    async addHumanControlLayer() {
      //     
      if (this.humanControlLayer) {
        this.canvas.remove(this.humanControlLayer);
        this.humanControlLayer = null;
      }
      if (!this.humanSvgGroup) return;

      const bounds = this.humanSvgGroup.getBoundingRect();
      const controlLayer = new Rect({
        left: this.humanSvgGroup.left,
        top: this.humanSvgGroup.top,
        width: bounds.width,
        height: bounds.height,
        originX: this.humanSvgGroup.originX,
        originY: this.humanSvgGroup.originY,
        fill: "rgba(0,0,0,0)", //  
        // fill: "rgba(0,0,255,0.2)", // :  
        selectable: true,
        evented: true,
        hasControls: false,
        hasBorders: false,
        padding: 20,
      });
      this.addCharacterControls(controlLayer);
      this.canvas.add(controlLayer);
      this.humanControlLayer = controlLayer;

      //     / 
      controlLayer.on("moving", () => {
        this.humanSvgGroup.set({
          left: controlLayer.left,
          top: controlLayer.top,
        });
        if (this.clothesSvgGroup) {
          const pos = this.selectedClothes?.position?.[this.currentPose] || {
            left: 0,
            top: 0,
          };
          this.clothesSvgGroup.set({
            left: controlLayer.left + pos.left * controlLayer.scaleX,
            top: controlLayer.top + pos.top * controlLayer.scaleY,
          });
        }
        this.canvas.renderAll();
      });

      //     / 
      controlLayer.on("scaling", () => {
        this.humanSvgGroup.set({
          scaleX: controlLayer.scaleX,
          scaleY: controlLayer.scaleY,
          left: controlLayer.left,
          top: controlLayer.top,
        });
        if (this.clothesSvgGroup) {
          const pos = this.selectedClothes?.position?.[this.currentPose] || {
            left: 0,
            top: 0,
          };
          this.clothesSvgGroup.set({
            scaleX: controlLayer.scaleX,
            scaleY: controlLayer.scaleY,
            left: controlLayer.left + pos.left * controlLayer.scaleX,
            top: controlLayer.top + pos.top * controlLayer.scaleY,
          });
        }
        this.canvas.renderAll();
      });

      //      / 
      controlLayer.on("rotating", () => {
        this.humanSvgGroup.set({
          angle: controlLayer.angle,
        });
        if (this.clothesSvgGroup) {
          this.clothesSvgGroup.set({
            angle: controlLayer.angle,
          });
        }
        this.canvas.renderAll();
      });

      // //     
      // this.humanSvgGroup.set({ selectable: false, evented: false });
      // if (this.clothesSvgGroup) {
      //   this.clothesSvgGroup.set({ selectable: false, evented: false });
      // }

      //  z-index 
      this.canvas.renderAll();
    },

    //    SVG  
    async addHumanSvg() {
      //   SVG  
      let prevLeft = 100;
      let prevTop = this.canvas.height / 2;
      let prevScaleX = 1;
      let prevScaleY = 1;
      let prevAngle = 0;
      if (this.humanSvgGroup) {
        //  , ,  
        prevLeft = this.humanSvgGroup.left;
        prevTop = this.humanSvgGroup.top;
        prevScaleX = this.humanSvgGroup.scaleX;
        prevScaleY = this.humanSvgGroup.scaleY;
        prevAngle = this.humanSvgGroup.angle;
        this.canvas.remove(this.humanSvgGroup);
        this.humanSvgGroup = null;
      }
      const humanSvgUrl = this.currentPose === "" ? humanArm : human;
      const loadedSVG = await loadSVGFromURL(humanSvgUrl);
      let svgGroup = util.groupSVGElements(loadedSVG.objects);
      svgGroup.set({
        left: prevLeft,
        top: prevTop,
        originX: "center",
        originY: "center",
        scaleX: prevScaleX,
        scaleY: prevScaleY,
        angle: prevAngle,
        selectable: true,
        evented: true,
        hasControls: false,
        hasBorders: false,
        lockMovementX: false,
        lockMovementY: false,
        lockScalingX: false,
        lockScalingY: false,
        lockRotation: false,
      });

      this.addCharacterControls(svgGroup);
      this.canvas.add(svgGroup);
      this.humanSvgGroup = svgGroup;
      this.addHumanMoveListener();
      await this.addHumanControlLayer();
      this.canvas.renderAll();
    },
    //      ( SVG/ )
    async selectClothes(clothes) {
      if (this.clothesSvgGroup) {
        this.canvas.remove(this.clothesSvgGroup);
      }
      const svgUrl =
        typeof clothes.svgUrl === "object"
          ? clothes.svgUrl[this.currentPose] || clothes.svgUrl[""]
          : clothes.svgUrl;
      const loadedSVG = await loadSVGFromURL(svgUrl);
      let svgGroup = util.groupSVGElements(loadedSVG.objects);

      //  SVG  +  position   / 
      const human = this.humanSvgGroup;
      const pos = clothes.position?.[this.currentPose] || { left: 0, top: 0 };

      //      
      svgGroup.set({
        left: human.left,
        top: human.top,
        originX: human.originX,
        originY: human.originY,
        scaleX: human.scaleX,
        scaleY: human.scaleY,
        angle: human.angle,
        selectable: false,
        evented: false,
        hasControls: false,
        hasBorders: false,
        lockMovementX: true,
        lockMovementY: true,
        lockScalingX: true,
        lockScalingY: true,
        lockRotation: true,
      });

      //    offset 
      svgGroup.forEachObject((obj) => {
        obj.left += pos.left;
        obj.top += pos.top;
      });
      svgGroup.setCoords();
      this.canvas.add(svgGroup);
      this.clothesSvgGroup = svgGroup;
      this.selectedClothes = clothes;
      this.canvas.renderAll();

      //     
      const idx = this.selectedColorIndexes[clothes.name] ?? 0;
      const btn = clothes.colorButtons[idx];
      if (btn) {
        this.changeClothesColors(btn.targets, clothes.name, idx);
      }
      await this.addHumanControlLayer();
    },
    //        
    addHumanMoveListener() {
      if (!this.humanSvgGroup) return;
      this.humanSvgGroup.set({ selectable: true, evented: true });

      //    
      this.humanSvgGroup.on("moving", () => {
        if (this.clothesSvgGroup && this.selectedClothes) {
          const pos = this.selectedClothes.position?.[this.currentPose] || {
            left: 0,
            top: 0,
          };
          this.clothesSvgGroup.set({
            left:
              this.humanSvgGroup.left + pos.left * this.humanSvgGroup.scaleX,
            top: this.humanSvgGroup.top + pos.top * this.humanSvgGroup.scaleY,
          });
          this.canvas.renderAll();
        }
      });

      //  ()    
      this.humanSvgGroup.on("scaling", () => {
        if (this.clothesSvgGroup && this.selectedClothes) {
          // scaleX, scaleY 
          this.clothesSvgGroup.set({
            scaleX: this.humanSvgGroup.scaleX,
            scaleY: this.humanSvgGroup.scaleY,
          });
          //    
          const pos = this.selectedClothes.position?.[this.currentPose] || {
            left: 0,
            top: 0,
          };
          this.clothesSvgGroup.set({
            left:
              this.humanSvgGroup.left + pos.left * this.humanSvgGroup.scaleX,
            top: this.humanSvgGroup.top + pos.top * this.humanSvgGroup.scaleY,
          });
          this.canvas.renderAll();
        }
      });
    },
    //    / SVG 
    async changePose(pose) {
      this.currentPose = pose;
      if (this.humanSvgGroup) this.canvas.remove(this.humanSvgGroup);
      if (this.clothesSvgGroup) this.canvas.remove(this.clothesSvgGroup);
      await this.addHumanSvg();
      this.addCharacterControls(this.humanSvgGroup);
      if (this.selectedClothes) {
        await this.selectClothes(this.selectedClothes);
      }
      await this.addHumanControlLayer();
    },
    //  SVG   (id/class  ,  )
    changeClothesColors(targets, clothesName, idx) {
      if (!this.clothesSvgGroup) return;
      //  
      function changeRecursive(obj) {
        if (obj._objects) {
          obj._objects.forEach((child) => changeRecursive(child));
        } else {
          targets.forEach(({ type, value, color }) => {
            if (
              (type === "id" &&
                (obj.id === value || (obj.get && obj.get("id") === value))) ||
              (type === "class" &&
                (obj.className === value ||
                  (obj.get && obj.get("class") === value)))
            ) {
              obj.set("fill", color);
            }
          });
        }
      }
      changeRecursive(this.clothesSvgGroup);
      this.canvas.requestRenderAll();

      //    
      this.selectedColorIndexes = {
        ...this.selectedColorIndexes,
        [clothesName]: idx,
      };
    },

    //     (  , : )
    saveCanvasAsImage() {
      let filename = prompt("   .", "");
      if (filename === null) return; //    
      if (!filename || filename.trim() === "") {
        filename = "";
      }
      if (!filename.endsWith(".jpg") && !filename.endsWith(".png")) {
        filename += ".jpg";
      }
      const dataUrl = this.canvas.toDataURL({
        format: "jpg",
        quality: 1.0,
      });
      const link = document.createElement("a");
      link.href = dataUrl;
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    //  SVG   (id )
    changeSvgColorsByIds(idColorList) {
      if (!this.svgGroup) return;
      this.svgGroup.forEachObject((obj) => {
        idColorList.forEach(({ id, color }) => {
          if (obj.id === id || obj.get("id") === id) {
            obj.set("fill", color);
          }
        });
      });
      this.canvas.requestRenderAll();
    },
    //  (//)  
    renderDeleteIcon(ctx, left, top, _styleOverride, fabricObject) {
      const size = 28;
      const padding = 16;
      const hitSize = size + padding * 2;
      const img = new Image();
      img.src = deleteIcon;
      ctx.save();
      ctx.translate(left, top);
      ctx.rotate(util.degreesToRadians(fabricObject.angle));
      ctx.globalAlpha = 0.7;
      ctx.drawImage(img, -size / 2, -size / 2, size, size);
      ctx.globalAlpha = 0.01;
      ctx.beginPath();
      ctx.arc(0, 0, hitSize / 2, 0, 2 * Math.PI);
      ctx.fillStyle = "#000";
      ctx.fill();
      ctx.globalAlpha = 1.0;
      ctx.restore();
    },
    renderRotateIcon(ctx, left, top, _styleOverride, fabricObject) {
      const size = 28;
      const padding = 16;
      const hitSize = size + padding * 2;
      const img = new Image();
      img.src = rotateIcon;
      ctx.save();
      ctx.translate(left, top);
      ctx.rotate(util.degreesToRadians(fabricObject.angle));
      ctx.globalAlpha = 0.7;
      ctx.drawImage(img, -size / 2, -size / 2, size, size);
      ctx.globalAlpha = 0.01;
      ctx.beginPath();
      ctx.arc(0, 0, hitSize / 2, 0, 2 * Math.PI);
      ctx.fillStyle = "#000";
      ctx.fill();
      ctx.globalAlpha = 1.0;
      ctx.restore();
    },
    renderResizeIcon(ctx, left, top, _styleOverride, fabricObject) {
      const size = 24;
      const padding = 16;
      const hitSize = size + padding * 2;
      const img = new Image();
      img.src = resizeIcon;
      ctx.save();
      ctx.translate(left, top);
      ctx.rotate(util.degreesToRadians(fabricObject.angle));
      ctx.globalAlpha = 0.7;
      ctx.drawImage(img, -size / 2, -size / 2, size, size);
      ctx.globalAlpha = 0.01;
      ctx.beginPath();
      ctx.arc(0, 0, hitSize / 2, 0, 2 * Math.PI);
      ctx.fillStyle = "#000";
      ctx.fill();
      ctx.globalAlpha = 1.0;
      ctx.restore();
    },
    //   
    deleteObject(_eventData, transform) {
      const canvasInstance = transform.target.canvas;
      //      
      if (
        transform.target.type === "i-text" &&
        transform.target._bgRect &&
        canvasInstance.contains(transform.target._bgRect)
      ) {
        canvasInstance.remove(transform.target._bgRect);
      }
      canvasInstance.remove(transform.target);
      canvasInstance.requestRenderAll();
    },
    addCharacterControls(obj) {
      if (obj.controls.deleteControl) delete obj.controls.deleteControl;
      obj.setControlsVisibility({
        tl: false,
        tr: false,
        bl: false,
        br: false,
        mt: false,
        mb: false,
        ml: false,
        mr: false,
        mtr: true, //  
        deleteControl: false, //   
        rotateControl: true, //  
        resizeControl: true, //  
      });

      obj.controls.rotateControl = new Control({
        x: 0,
        y: -0.5,
        offsetY: -40,
        cursorStyle: "crosshair",
        render: this.renderRotateIcon,
        cornerSize: 28,
        actionHandler: controlsUtils.rotationWithSnapping,
        hitbox: { width: 60, height: 60 },
      });
      obj.controls.resizeControl = new Control({
        x: 0.5,
        y: 0.5,
        cursorStyle: "se-resize",
        render: this.renderResizeIcon,
        cornerSize: 28,
        actionHandler: controlsUtils.scalingEqually,
        hitbox: { width: 60, height: 60 },
      });

      obj.set({
        cornerColor: "#00000000",
        cornerSize: 18,
        cornerStyle: "circle",
        borderColor: "#00bfff",
        borderDashArray: [6, 4],
        transparentCorners: false,
        hasBorders: false,
        hasControls: false,
      });

      obj.on("selected", () => {
        obj.set({ hasBorders: true, hasControls: true });
        obj.setControlsVisibility({
          tl: false,
          tr: false,
          bl: false,
          br: false,
          mt: false,
          mb: false,
          ml: false,
          mr: false,
          mtr: true,
          deleteControl: false, //   
          rotateControl: true,
          resizeControl: true,
        });
        obj.canvas.requestRenderAll();
      });
      obj.on("deselected", () => {
        obj.set({ hasBorders: false, hasControls: false });
        obj.setControlsVisibility({
          tl: false,
          tr: false,
          bl: false,
          br: false,
          mt: false,
          mb: false,
          ml: false,
          mr: false,
          mtr: true,
          deleteControl: false, //   
          rotateControl: true,
          resizeControl: true,
        });
        obj.canvas.requestRenderAll();
      });
    },
    //   
    addCustomControls(obj) {
      obj.setControlsVisibility({
        tl: false,
        tr: false,
        bl: false,
        br: false,
        mt: false,
        mb: false,
        ml: false,
        mr: false,
        mtr: true,
        deleteControl: true,
        rotateControl: true,
        resizeControl: true,
      });

      obj.controls.deleteControl = new Control({
        x: 0.5,
        y: -0.5,
        cursorStyle: "pointer",
        mouseUpHandler: this.deleteObject,
        render: this.renderDeleteIcon,
        cornerSize: 28,
        hitbox: { width: 60, height: 60 },
      });
      obj.controls.rotateControl = new Control({
        x: 0,
        y: -0.5,
        offsetY: -40,
        cursorStyle: "crosshair",
        render: this.renderRotateIcon,
        cornerSize: 28,
        actionHandler: controlsUtils.rotationWithSnapping,
        hitbox: { width: 60, height: 60 },
      });
      obj.controls.resizeControl = new Control({
        x: 0.5,
        y: 0.5,
        cursorStyle: "se-resize",
        render: this.renderResizeIcon,
        cornerSize: 28,
        actionHandler: controlsUtils.scalingEqually,
        hitbox: { width: 60, height: 60 },
      });

      obj.set({
        cornerColor: "#00000000",
        cornerSize: 18,
        cornerStyle: "circle",
        borderColor: "#00bfff",
        borderDashArray: [6, 4],
        transparentCorners: false,
        hasBorders: false,
        hasControls: false,
        // editable: true, //     !
      });

      obj.on("selected", () => {
        obj.set({ hasBorders: true, hasControls: true });
        obj.setControlsVisibility({
          tl: false,
          tr: false,
          bl: false,
          br: false,
          mt: false,
          mb: false,
          ml: false,
          mr: false,
          mtr: true,
          deleteControl: true,
          rotateControl: true,
          resizeControl: true,
        });
        obj.canvas.requestRenderAll();
      });
      obj.on("deselected", () => {
        obj.set({ hasBorders: false, hasControls: false });
        obj.setControlsVisibility({
          tl: false,
          tr: false,
          bl: false,
          br: false,
          mt: false,
          mb: false,
          ml: false,
          mr: false,
          mtr: true,
          deleteControl: true,
          rotateControl: true,
          resizeControl: true,
        });
        obj.canvas.requestRenderAll();
      });
    },
    // () 
    addRectangle() {
      const rect = new Rect({
        left: this.canvas.width / 2,
        top: this.canvas.height / 2,
        fill: "#ffe066",
        width: 200,
        height: 200,
        objectCaching: false,
        originX: "center",
        originY: "center",
        centeredRotation: true,
        padding: 16,
        selectable: true,
        evented: true,
        hasControls: false,
        hasBorders: false,
      });
      this.addCustomControls(rect);
      this.canvas.add(rect);
      this.canvas.setActiveObject(rect);
      this.canvas.renderAll();
    },
    //  
    addTextbox() {
      const text = "1.  ";
      const padding = 24;
      const textbox = new IText(text, {
        left: this.canvas.width / 2,
        top: this.canvas.height / 2,
        originX: "center",
        originY: "center",
        fontSize: 32,
        fill: "#222",
        fontFamily: "sans-serif",
        editable: true,
        selectable: true,
        evented: true,
        hasControls: false,
        hasBorders: false,
        backgroundColor: "transparent",
        textAlign: "center",
      });

      //   
      const bgRect = new Rect({
        left: textbox.left,
        top: textbox.top,
        originX: "center",
        originY: "center",
        width: textbox.width + padding * 2,
        height: textbox.height + padding * 2,
        rx: 18,
        ry: 18,
        fill: "#fffbe6",
        selectable: false,
        evented: false,
        hasBorders: false,
        hasControls: false,
      });

      //    
      textbox._bgRect = bgRect;

      this.canvas.add(bgRect);
      this.canvas.add(textbox);

      //         
      const ensureBgBelowText = () => {
        const objs = this.canvas.getObjects();
        const bgIdx = objs.indexOf(bgRect);
        const textIdx = objs.indexOf(textbox);
        if (bgIdx > -1 && textIdx > -1 && bgIdx !== textIdx - 1) {
          objs.splice(bgIdx, 1);
          objs.splice(textIdx, 0, bgRect);
          this.canvas._objects = objs;
        }
      };

      //  ///       
      const updateBgRect = () => {
        textbox.setCoords();
        bgRect.set({
          width: textbox.width * textbox.scaleX + padding * 2,
          height: textbox.height * textbox.scaleY + padding * 2,
          left: textbox.left,
          top: textbox.top,
          scaleX: 1,
          scaleY: 1,
          angle: textbox.angle,
        });
        ensureBgBelowText();
        this.canvas.requestRenderAll();
      };

      //  
      textbox.on("changed", updateBgRect);
      textbox.on("scaling", updateBgRect);
      textbox.on("moving", updateBgRect);
      textbox.on("rotating", updateBgRect);

      //  / 
      ensureBgBelowText();

      this.addCustomControls(textbox);
      this.canvas.setActiveObject(textbox);
      this.canvas.renderAll();
    },

    //   ( 3)
    addImage(imageSrc) {
      const img = new window.Image();
      img.src = imageSrc;
      img.onload = () => {
        let width = img.width;
        let height = img.height;
        //  400px    
        if (width > 400) {
          const scale = 400 / width;
          width = 400;
          height = height * scale;
        }
        const fabricImage = new FabricImage(img, {
          left: this.canvas.width / 2,
          top: this.canvas.height / 2,
          originX: "center",
          originY: "center",
          width,
          height,
          selectable: true,
          evented: true,
          hasControls: false,
          hasBorders: false,
        });
        this.addCustomControls(fabricImage);
        this.canvas.add(fabricImage);
        this.canvas.setActiveObject(fabricImage);
        this.canvas.renderAll();
      };
    },
    //    
    onFileChange(event) {
      const files = event.target.files;
      if (!files || files.length === 0) return;

      // "  "  (selectable !== false)
      let imageCount = this.canvas
        .getObjects()
        .filter(
          (obj) => obj.type === "image" && obj.selectable !== false
        ).length;

      if (imageCount >= 3) {
        alert("  3   .");
        return;
      }

      //     
      const file = files[0];
      if (!file) return;

      const reader = new FileReader();
      reader.onload = (e) => {
        const img = new window.Image();
        img.src = e.target.result;
        img.onload = () => {
          let width = img.width;
          let height = img.height;
          let scale = 1;
          //  400px     (scale )
          if (width > 400) {
            scale = 400 / width;
          }
          const fabricImage = new FabricImage(img, {
            left: this.canvas.width / 2,
            top: this.canvas.height / 2,
            originX: "center",
            originY: "center",
            //  width/height 
            width: width,
            height: height,
            scaleX: scale,
            scaleY: scale,
            selectable: true,
            evented: true,
            hasControls: false,
            hasBorders: false,
          });
          this.addCustomControls(fabricImage);
          this.canvas.add(fabricImage);
          this.canvas.setActiveObject(fabricImage);
          this.canvas.renderAll();
        };
      };
      reader.readAsDataURL(file);
    },
    //   
    replaceDefaultImage(newImageSrc) {
      if (!this.defaultImageObject) return;
      const img = new window.Image();
      img.src = newImageSrc;
      img.onload = () => {
        this.defaultImageObject.setElement(img);
        this.defaultImageObject.set({
          width: 200,
          height: 200,
          scaleX: 1,
          scaleY: 1,
        });
        this.canvas.renderAll();
      };
    },
    //   input 
    triggerFileInput() {
      this.$refs.fileInput.click();
    },

    //      
    async initializeCanvas() {
      this.canvas = markRaw(
        new Canvas(this.$refs.canvas, {
          width: window.innerWidth,
          height: window.innerHeight,
          selection: true,
          backgroundColor: "#ffffff",
        })
      );
      this.canvas.on("after:render", () => {
        const ctx = this.canvas.getContext();
        const objs = this.canvas.getObjects();
        objs.forEach((obj, idx) => {
          //    
          const p = obj.getPointByOrigin
            ? obj.getPointByOrigin("left", "top")
            : { x: obj.left, y: obj.top };
          ctx.save();
          ctx.font = "bold 18px sans-serif";
          ctx.fillStyle = "red";
          ctx.strokeStyle = "white";
          ctx.lineWidth = 3;
          ctx.strokeText(idx + "", p.x + 8, p.y + 24);
          ctx.fillText(idx + "", p.x + 8, p.y + 24);
          ctx.restore();
        });
      });
      //   
      const img = new window.Image();
      img.src = this.backroundList[0].src;
      img.onload = async () => {
        const fabricImage = new FabricImage(img, {
          left: this.canvas.width / 2,
          top: this.canvas.height / 2,
          originX: "center",
          originY: "center",
          width: 200,
          height: 200,
          selectable: false,
          evented: false,
          hasControls: false,
          hasBorders: false,
          lockMovementX: true,
          lockMovementY: true,
          lockScalingX: true,
          lockScalingY: true,
          lockRotation: true,
        });
        this.canvas.add(fabricImage);
        this.defaultImageObject = fabricImage;
        this.canvas.renderAll();

        //      
        await this.addHumanSvg();
        await this.addHumanControlLayer();
      };

      // mouse:up    !
      this.canvas.on("mouse:down", (opt) => {
        const target = this.canvas.getActiveObject();
        const objs = this.canvas.getObjects();

        // 1.     
        if (this.defaultImageObject) {
          const idx = objs.indexOf(this.defaultImageObject);
          if (idx > -1) {
            objs.splice(idx, 1);
            objs.unshift(this.defaultImageObject);
          }
        }

        // ( SVG )      !
        if (target === this.humanSvgGroup) {
          [
            this.humanSvgGroup,
            this.humanControlLayer,
            this.clothesSvgGroup,
          ].forEach((obj) => {
            const idx = objs.indexOf(obj);
            if (idx > -1) objs.splice(idx, 1);
          });
          if (this.humanSvgGroup) objs.push(this.humanSvgGroup);
          if (this.humanControlLayer) objs.push(this.humanControlLayer);
          if (this.clothesSvgGroup) objs.push(this.clothesSvgGroup);

          this.canvas._objects = objs;
          this.canvas.renderAll();
          return;
        }

        // (IText)       !
        if (target && target.type === "i-text" && target._bgRect) {
          //    
          [target._bgRect, target].forEach((obj) => {
            const idx = objs.indexOf(obj);
            if (idx > -1) objs.splice(idx, 1);
          });
          //      push
          objs.push(target._bgRect);
          objs.push(target);

          this.canvas._objects = objs;
          this.canvas.renderAll();
          return;
        }

        // 2.  3       !
        const isHumanPart = [
          this.humanControlLayer,
          this.humanSvgGroup,
          this.clothesSvgGroup,
        ].includes(target);

        if (isHumanPart) {
          [
            this.humanSvgGroup,
            this.humanControlLayer,
            this.clothesSvgGroup,
          ].forEach((obj) => {
            const idx = objs.indexOf(obj);
            if (idx > -1) objs.splice(idx, 1);
          });
          if (this.humanSvgGroup) objs.push(this.humanSvgGroup);
          if (this.humanControlLayer) objs.push(this.humanControlLayer);
          if (this.clothesSvgGroup) objs.push(this.clothesSvgGroup);
        } else if (
          target &&
          ![
            this.humanControlLayer,
            this.humanSvgGroup,
            this.clothesSvgGroup,
            this.defaultImageObject,
          ].includes(target)
        ) {
          const idx = objs.indexOf(target);
          if (idx > -1) {
            objs.splice(idx, 1);
            objs.push(target);
          }
        }

        this.canvas._objects = objs;
        this.canvas.renderAll();
      });
    },
  },
  //     
  mounted: async function () {
    await this.initializeCanvas();
  },
};
</script>

<style scoped>
.edit-panel {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 60px; /*   */
  background: #fff;
  border-top-left-radius: 18px;
  border-top-right-radius: 18px;
  box-shadow: 0 -2px 12px rgba(0, 0, 0, 0.12);
  transition: height 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 2000;
  overflow: hidden;
  touch-action: none;
}
.edit-panel.open {
  height: 40vh; /*   */
}
.panel-handle {
  width: 48px;
  height: 6px;
  background: #ccc;
  border-radius: 3px;
  margin: 10px auto 12px auto;
  cursor: pointer;
  touch-action: none;
}
.panel-content {
  padding: 0 16px 16px 16px;
  overflow-y: auto;
  height: calc(60vh - 32px);
}
</style>
